import React from 'react';
import { convertToMelodyNote, flat, getLibray, sharp } from '../base/base-lib';
import { FindExactMatch } from '../features/composer/composerSlice';
import { FormGroup } from './FormGroup';
import StaffNotes from './music/StaffNotes';
import { InvertVoice, invertVoices } from '../base/datasourcegenerator';
export class QuickInput extends React.Component {
    constructor(props) {
        super(props);
        this.state = { txt: '', patterns: [], chords: [], measureLength: '.5' };
    }
    render() {
        let { props } = this;
        return (
            <div style={{
                flex: '1 1 0%',
                display: 'flex',
                flexDirection: 'column',
                height: '100%',
                position: 'relative'
            }}>
                <div className="row" style={{
                    display: 'flex',
                    flexDirection: 'column',
                    position: 'relative'
                }}>
                    <div className="col-md-12">
                        <div className="row">
                            <div className="col-md-4">
                                <FormGroup>
                                    <label>Name</label>
                                    <input
                                        value={this.state.txt}
                                        onChange={(e) => {
                                            let foundMatches = this.handleChordTextChange(e);

                                            this.setState({
                                                txt: e.currentTarget.value,
                                                chords: foundMatches
                                            })
                                        }} type="text" className="form-control" placeholder="Enter ..." />
                                </FormGroup>
                                <FormGroup>
                                    <span>Current Chord count : {this.state.chords.length}</span>
                                </FormGroup>
                            </div>
                            <div className="col-md-4">
                                <FormGroup>
                                    <select className="form-control" value={this.state.txt} onChange={(val) => {
                                        console.log(val);
                                        if (val && val.currentTarget) {
                                            let foundMatches = this.handleChordTextChange(val)
                                            if (foundMatches) {
                                                this.setState({
                                                    txt: val.currentTarget.value,
                                                    chords: foundMatches
                                                })
                                            }
                                        }
                                    }}>
                                        <option>Select</option>
                                        {(this.props.chordProgressions || []).filter(v => v.progression).map(v => {
                                            return <option value={v.progression.join(' ')}>{v.name}</option>
                                        })}
                                    </select>
                                </FormGroup>
                                <FormGroup>
                                    <select className="form-control" value={this.state.measureLength} onChange={(val) => {
                                        console.log(val);
                                        this.setState({ measureLength: val.currentTarget.value })
                                    }}>
                                        {[.5, 1, 2, 3, 4, 8].map(v => {
                                            return <option key={v} value={v}>{v}</option>
                                        })}
                                    </select>
                                </FormGroup>
                            </div>
                            <div className="col-md-4">
                                <FormGroup>
                                    <select className="form-control" value={this.state.selectedPattern} onChange={(val) => {
                                        console.log(val);
                                        this.setState({ selectedPattern: val.currentTarget.value })
                                    }}>
                                        {[{ title: 'Arpeggio Up', value: 'arpeggio-up' },
                                        { title: 'Arpeggio Down', value: 'arpeggio-down' }].map(v => {
                                            return <option key={v.title} value={v.value}>{v.title}</option>
                                        })}
                                    </select>
                                    <button type="button" onClick={() => {
                                        if (this.state.selectedPattern) {
                                            this.setState({
                                                patterns: [... (this.state.patterns || []), this.state.selectedPattern]
                                            });
                                        }
                                    }} className="btn btn-success" title="Add"><i className="fas fa-plus"></i></button>
                                </FormGroup>
                            </div>
                        </div>

                        <FormGroup>
                            <div class="btn-group">
                                <button type="button" onClick={() => {
                                    if (this.props.buildSceneProgressions) {
                                        this.props.buildSceneProgressions({
                                            measureLength: parseFloat(this.state.measureLength),
                                            chords: this.state.chords,
                                            dontPlayChords: true
                                        });
                                    }
                                }} className="btn btn-success" title="Build Scenes without chords"><i className="fas fa-viruses"></i></button>
                                {/* <button type="button" onClick={() => {
                                    if (this.props.buildSceneProgressions) {
                                        this.props.buildSceneProgressions({
                                            measureLength: parseFloat(this.state.measureLength),
                                            chords: this.state.chords
                                        });
                                    }
                                }} className="btn btn-success" title="Build Scenes with chords"><i className="fas fa-bolt"></i></button>
                                <button type="button" onClick={() => {
                                    if (this.props.buildSceneProgressions) {
                                        this.props.buildSceneProgressions({
                                            measureLength: parseFloat(this.state.measureLength),
                                            chords: this.state.chords,
                                            pattern: ['arpeggio-up']
                                        });
                                    }
                                }} className="btn btn-success" title="Build Scenes with chords"><i className="fas fa-bolt"></i></button> */}
                            </div>
                        </FormGroup>
                    </div>
                </div>
                <div className="row" style={{
                    overflow: 'auto',
                    margin: 0,
                    flex: '1 1 0%'
                }}>
                    <div
                        className={"col-md-12"}
                        style={{
                            flexDirection: 'row',
                            display: 'flex',
                            flexWrap: 'wrap'
                        }}>
                        {this.state.chords.map((info, index) => {
                            let { chordInfo, options, values, value_voicing, value_letter, voicing_letter } = info;
                            if (options && options.length) {
                                return [
                                    <div key={`qi-contain-${index}`} style={{ flex: 1, flexDirection: 'row', display: 'flex' }}>
                                        <StaffNotes arg={chordInfo} onPlay={(t) => {
                                            if (props.onPlay) {
                                                props.onPlay({ value: t, play: true });
                                            }
                                        }} onStop={(t) => {
                                            if (props.onStop) {
                                                props.onStop({ value: t, play: false });
                                            }
                                        }}
                                            onAddToBank={(t, arg) => {
                                                if (props.onAddToBank) {
                                                    props.onAddToBank({ noteLetter: value_letter, notes: t, chord: arg });
                                                }
                                            }}
                                            bias={this.props.bias ? sharp : flat} key={`qi-${index}`} value={values} />
                                        <StaffNotes arg={chordInfo} onPlay={(t) => {

                                            if (props.onPlay) {
                                                props.onPlay({ value: t, play: true });
                                            }
                                        }} onStop={(t) => {
                                            if (props.onStop) {
                                                props.onStop({ value: t, play: false });
                                            }
                                        }}
                                            onAddToBank={(t, arg) => {
                                                if (props.onAddToBank) {
                                                    props.onAddToBank({ noteLetter: voicing_letter, notes: t, chord: arg });
                                                }
                                            }}
                                            bias={this.props.bias ? sharp : flat}
                                            key={`qi-voicing-${index}`} value={{ ...value_voicing }} />
                                    </div>]
                            }
                        }).filter(x => x)}
                    </div>
                </div>
            </div >
        )
    }

    handleChordTextChange(e) {
        let foundMatches = [];
        let currentChords = processChordString(e.currentTarget.value);
        currentChords.forEach((chordInfo) => {
            let v = [0];
            switch (chordInfo.type) {
                case 'minor':
                    v.push(3, 7);
                    break;
                case 'major':
                    v.push(4, 7);
                    break;
                case 'suspended':
                    v.push(5, 7);
                    break;
                case 'diminished':
                    v.push(3, 6);
                    break;
            }
            switch (chordInfo.chordDescription) {
                case '7':
                    v.push('A');
                    break;
                case '6':
                    v.push(9);
                    break;
                case '9':
                    v.push('A', 14);
                    break;
                case '11':
                    v.push('A', 14, 17);
                    break;
                case '13':
                    v.push('A', 14, 17, 21);
                    break;
            }
            let v_temp = v.map(v_t => typeof (v_t) === 'string' ? parseInt(v_t, 12).toString(12) : `${v_t % 12}`.toString(12));
            let temp = FindExactMatch(v_temp.sort((a, b) => a - b).map(v => v.toString()), []).filter(x => !x.smartinfo.isInversion);
            if (temp.length > 1) {
                let ttemp = temp.filter(x => x.smartinfo.name.indexOf('+') === -1);
                if (ttemp.length > 0) {
                    temp = ttemp;
                }
            }

            console.log(temp);
            let offset = getLibray()[chordInfo.key.toString() + chordInfo.accidental];
            let values = {};
            let value_voicing = {};
            let value_letter;
            if (temp[0]) {
                temp[0].smartinfo.voice.map((te, index) => {

                    let num = (12 * (chordInfo.octave || 3)) + offset + te;
                    values[num] = true;
                    if (index === 0) {
                        value_letter = convertToMelodyNote(num, this.props.bias);
                    }
                });
            }

            let invertedVoice = [];
            if (chordInfo.isInversion) {
                let v_nums = v.map(v => typeof (v) === 'string' ? parseInt(`${v}`, 36) : v);
                invertedVoice = InvertVoice(v_nums, chordInfo.inversion);
                console.log('inverted voice');
                console.log(invertedVoice);
                offset += v_nums[chordInfo.inversion];
                v = invertedVoice;
            }
            let voicing_letter;
            let dropped_numbers = []
            if (chordInfo.overKey) {
                let over_offset = getLibray()[chordInfo.overKey.toString() + (chordInfo.overAccidental || '')];
                if (over_offset < offset) {
                    over_offset = over_offset - offset + 12
                }
                else {
                    over_offset = over_offset - offset
                }
                let over_num = (12 * (chordInfo.octave || 3)) + over_offset + offset;
                dropped_numbers.push(over_num);
            }
            v.map(v => typeof (v) === 'string' ? parseInt(`${v}`, 36) : v).map((te, index) => {

                let num = (12 * (chordInfo.octave || 3)) + offset + te;
                if (dropped_numbers.indexOf(num) !== -1) {
                    num -= 12;
                }
                value_voicing[num] = true;
                if (index === 0) {
                    voicing_letter = convertToMelodyNote(num, this.props.bias);
                }
            });
            foundMatches.push({
                chordInfo,
                voicing_letter,
                value_letter,
                options: temp,
                offset,
                value_voicing,
                values,
                droppedNotes: dropped_numbers
            });
        });
        return foundMatches;
    }
}

// Giant Steps 
// BM7	D7  GM7	Bb7  EbM7	Am7  D7  GM7  Bb7  EbM7  F#7  BM7  Fm7  Bb7  EbM7	 Am7  D7  GM7	 C#m7  F#7  BM7	 Fm7  Bb7  EM7	 C#m7  F#7 
function processChordString(str) {

    const regex = /(?<key>[A-G])(?<accidental>[b|#]?)(?<type>Maj|Min|sus|Sus|Dim|dim|o|m|M|d|D|s|S|)?(?<rootchord>10|11|12|13|14|15|16|17|19|18|1|2|3|4|5|6|7|8|9)*(?<over>\/(?<overkey>[A-G])(?<overaccidental>[b|#]?))*(?<chordaddition>([b|#](10|11|12|13|14|15|16|17|19|18|1|2|3|4|5|6|7|8|9)))?(?<chordaddition2>([b|#](10|11|12|13|14|15|16|17|19|18|1|2|3|4|5|6|7|8|9)))?(?<chordaddition3>([b|#](10|11|12|13|14|15|16|17|19|18|1|2|3|4|5|6|7|8|9)))?(?<inversion> (inv)* (1|2|3|4|5|6|7|8|9))*(?<octave>( \/(1|2|3|4|5|6|7|8|9)))*/gm;
    //     const str = `BM7
    // D7
    // Bb7`;
    let m;
    let res = [];
    while ((m = regex.exec(str)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (m.index === regex.lastIndex) {
            regex.lastIndex++;
        }

        let result = {
            key: '',
            accidental: '',
            type: 'major',
            changes: [],
            octave: 4,
            inversion: 0,
            isInversion: false,
            chordDescription: '',
            userMade: true
        }
        if (m.groups) {
            result.key = m.groups.key;
            result.name = m.groups.key

            switch (m.groups.accidental) {
                case 'b':
                    result.accidental = flat;
                    break;
                case '#':
                    result.accidental = sharp;
                    break;
            }
            result.name = `${result.name}${result.accidental}`;

            switch (m.groups.type) {
                case 'Maj':
                case 'M':
                    result.type = 'major';
                    result.name = `${result.name} Maj`;
                    break;
                case 'Min':
                case 'm':
                    result.type = 'minor'
                    result.name = `${result.name} min`;
                    break;
                case 'sus':
                case 'Sus':
                case 'Sus':
                case 's':
                case 'S':
                    result.type = 'suspended';
                    result.name = `${result.name} sus`;
                case 'Dim':
                case 'dim':
                case 'd':
                case 'o':
                    result.type = 'diminished';
                    result.name = `${result.name} dim`;
                    break;

            }

            if (m.groups.chordaddition) {
                result.changes.push(m.groups.chordaddition)
            }
            if (m.groups.chordaddition2) {
                result.changes.push(m.groups.chordaddition2)
            }
            if (m.groups.chordaddition3) {
                result.changes.push(m.groups.chordaddition3)
            }

            if (m.groups.rootchord) {
                result.chordDescription = m.groups.rootchord;
                result.name = `${result.name} ${result.chordDescription}`;
            }
            if (m.groups.overkey) {
                result.overKey = m.groups.overkey;
                if (m.groups.overaccidental) {
                    switch (m.groups.overaccidental) {
                        case 'b':
                            result.overAccidental = flat;
                            break;
                        case '#':
                            result.overAccidental = sharp;
                            break;
                    }
                }
                result.name = `${result.name}/${result.overKey}${result.overAccidental ? result.overAccidental : ''}`;
            }
            if (m.groups.octave) {
                result.octave = parseInt(m[19], 10);
                if (isNaN(result.octave)) {
                    result.octave = 4;
                }
                result.octave -= 1;
            }
            if (m.groups.inversion) {
                result.inversion = parseInt(m[16], 10);
                if (!isNaN(result.inversion)) {
                    result.isInversion = true;
                    let postfix = '';
                    switch (result.inversion) {
                        case 1:
                            postfix = 'st';
                            break;
                        case 2:
                            postfix = 'nd';
                            break
                        case 3:
                            postfix = 'rd';
                            break
                        case 4:
                        default:
                            postfix = 'th';
                            break
                    }
                    result.name = `${result.name} inv ${result.inversion}${postfix}`;
                }
            }
        }
        // The result can be accessed through the `m`-variable.
        else {
            m.forEach((match, groupIndex) => {
                console.log(`Found match, group ${groupIndex}: ${match}`);
                switch (groupIndex) {
                    case 1:
                        result.key = match;
                        break;
                    case 2:
                        result.accidental = match || '';
                        if (match === 'b') {
                            result.accidental = flat;
                        }
                        else if (match === '#') {
                            result.accidental = sharp;
                        }

                        break;
                    case 3:
                        switch (match || '') {
                            case 'm':
                                result.type = 'minor';
                                break;
                            case 'M':
                                result.type = 'major';
                                break;
                            case 's':
                            case 'S':
                                result.type = 'suspended';
                                break;
                            case 'd':
                            case 'D':
                                result.type = 'diminished';
                                break;
                        }
                        break;
                    case 4:
                        result.chordDescription = match;
                        break;
                }
            });
        }
        if (result.key) {
            res.push(result);
        }
    }

    return res;
}