import React from 'react';
import { convertToMelodyNote, findLetter } from '../../base/base-lib';
import ChordMaster, { calculateV } from '../../base/chord-master';
import { FormGroup } from '../FormGroup';
import ScaleNotes from './ScaleNotes';
import StaffNotes from './StaffNotes';


export default class MidiTrack extends React.Component {
    reader = null;
    constructor(props) {
        super(props);
        this.state = {
            canvasId: `c_${Math.random()}`
        }
    }
    shouldComponentUpdate(nextProps, props) {
        this.draw();
        return false;
    }
    draw() {
        this.drawAgain = true;
        let { height = 100 } = this.props;
        let padding = 10;
        let updateDraw = () => {
            if (this.drawAgain) {
                this.drawAgain = false;
                var canvas = document.getElementById(this.state.canvasId);
                if (canvas && canvas.getContext) {
                    var ctx = canvas.getContext('2d');
                    canvas.width = canvas.parentElement.getBoundingClientRect().width - 2 * padding;
                    this.drawContainer(canvas, ctx)
                }
            }
            if (this.drawAgain) {
                updateDraw();
            }
        };
        requestAnimationFrame(updateDraw)
    }
    drawContainer(canvas, ctx) {
        let { height = 100 } = this.props;
        let outerPadding = 20;
        ctx.fillStyle = 'rgb(225, 225, 225)';
        ctx.strokeRect(outerPadding, outerPadding, canvas.width - (outerPadding * 2), height - (outerPadding * 2));
        if (this.props.track) {
            this.drawTrack(canvas, ctx, {
                x: outerPadding,
                y: outerPadding,
                width: canvas.width - (outerPadding * 2),
                height: height - (outerPadding * 2)
            });
            this.drawSelection(ctx, {
                x: outerPadding,
                y: outerPadding,
                width: canvas.width - (outerPadding * 2),
                height: height - (outerPadding * 2)
            })
            ctx.font = '15px serif';
            ctx.fillStyle = 'rgb(255, 225, 225)';
            ctx.fillText(this.props.track.name, 10, 10);
        }
    }
    drawSelection(ctx, args) {

        // let { x, y, width, height } = args;
        // let { selection_start, selection_end } = this.props;
        // if (selection_end !== undefined && selection_start !== undefined) {

        //     // Turn transparency on
        //     ctx.globalAlpha = 0.2;
        //     var v = x + width * (selection_start / 100)
        // }
    }
    drawTrack(canvas, ctx, args) {
        let { x, y, width, height } = args;
        let { start, end } = this.props;
        let total_duration = this.props.end - this.props.start;
        if (this.props.track && this.props.track.notes) {
            this.props.track.notes.map(v => {
                if ((v.time + v.duration) >= this.props.start && (v.time) < this.props.end) {
                    ctx.fillStyle = 'rgb(0, 225, 225)';
                    let widthRelative = v.duration / total_duration;
                    let relativeHeight = 1 / 127;
                    let relativeY = v.midi / 127;
                    let actual_y = relativeY * height + y;
                    let relativeX = (v.time - start) / (end - start);
                    let actual_x = relativeX * width + x;
                    ctx.fillRect(actual_x, actual_y, width * widthRelative, height * relativeHeight);
                }
            })
        }
    }

    render() {
        const { props } = this;
        const { nextBank, banks } = this.props;

        if (props.track && props.track.notes && props.track.notes.length) {
            return (
                <div style={{ padding: 10 }}>
                    <canvas id={this.state.canvasId} height={this.props.height || 100}></canvas>
                </div>
            )
        }
        return <div></div>;
    }
}
