import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import MidiFile from './MidiFile';

import {
    selectCurrentMidiFile,
    selectMidiFileName,
    updateMidiFileBreaks,
    updateMidiFileSelection,
    getMidiFileData
} from './composerSlice';
import {
    exportToSerato,
    loadComposerCompanionStorage,
    newComposerCompanionStorage,
    saveComposerCompanionStorage,
    loadMidiFile
} from './composerSlice';
import { Slider } from '../../components/Slider';
import * as midiService from '../../base/midi-service';
import Panel from '../../components/panel/Panel';
import MidiTrack from '../../components/music/MidiTrack';
import { readFile } from '../../base/util';

export function MidiViewer() {
    const dispatch = useDispatch();
    const midiFiles = useSelector(selectMidiFileName);
    const currentMidiFile = useSelector(selectCurrentMidiFile)
    const songLength = currentMidiFile ? midiService.getSongLength(currentMidiFile) : null;
    const songName = currentMidiFile ? midiService.getSongName(currentMidiFile) : null;
    let songDataSelector = useSelector(getMidiFileData);
    const songData = currentMidiFile ? songDataSelector(currentMidiFile) : null;

    const tracks = currentMidiFile ? midiService.getTracks(currentMidiFile) : null;
    let start = 0;
    let end = 0;
    let selection_start = 0;
    let selection_end = 0;
    if (songData) {
        start = Math.min(...songData.breaks.map(v => v.val))
        end = Math.max(...songData.breaks.map(v => v.val));
        selection_start = Math.min(...songData.selections.map(v => v.val));
        selection_end = Math.max(...songData.selections.map(v => v.val));
    }
    return (
        <Panel stretch title={songName || currentMidiFile || 'Midi Viewer'}>
            <Slider title={`${start}s`} menu={`${end}s`} relative min={0} step={.1} widthProcessor={(v) => {
                return v - 35;
            }} value={songData ? songData.breaks : null} max={songLength} onChange={(breaks) => {
                dispatch(updateMidiFileBreaks({ midi: currentMidiFile, breaks }))
            }} />
            <Slider title={`${selection_start}`} menu={`${selection_end}`} relative min={0} step={.01} widthProcessor={(v) => {
                return v - 35;
            }} value={songData ? songData.selections : null} max={100} onChange={(breaks) => {
                dispatch(updateMidiFileSelection({ midi: currentMidiFile, breaks }))
            }} />
            {
                tracks ? tracks.map((track, trackIndex) => {
                    return <MidiTrack
                        start={start}
                        end={end}
                        selection_start={selection_start}
                        selection_end={selection_end}
                        track={track}
                        trackIndex={trackIndex}
                        key={`midi-track-${trackIndex}`} />
                }) : null
            }
        </Panel >
    );
}


