export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      NLE: true,
      'Chord Input': true,
      ScaleOptionList: true,
      ChordOptionLists: true,
      OnDeckPallettes: true,
      QuickInputController: true
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ loaded: true })
    }, 4000)
  }
  enabled(key) {
    return this.state[key] ? 'text-success' : 'text-danger';
  }
  isEnabled(key) {
    return this.state[key] ? true : false;
  }
  toggle(key) {
    this.setState({
      [key]: !this.state[key]
    })
  }
  render() {

    return (
      <div className="wrapper">
        <Preloader hide={this.state.loaded} />
        <Navbar >
          <NavbarHolder>
            <NavbarButton title="Temporary" />
          </NavbarHolder>
          <NavbarHolder right />
        </Navbar>
        <SidebarContainer >
          <Sidebar >
            <SidebarMenu>
              <NewButton />
              <SaveButton />
              <FileLoader />
              <ExportSerato />
            </SidebarMenu>
            <SidebarMenu>
              <SidebarMenuItem title="Modules" >
                <SidebarMenuItem title="Composer Header" icon={`nav-icon far fa-circle ${this.enabled('ComposerHeader')}`} onClick={() => {
                  this.toggle('ComposerHeader');
                }} />
                <SidebarMenuItem title="Scenes" icon={`nav-icon far fa-circle ${this.enabled('Scenes')}`} onClick={() => {
                  this.toggle('Scenes');
                }} />
                <SidebarMenuItem title="Scene" icon={`nav-icon far fa-circle ${this.enabled('Scene')}`} onClick={() => {
                  this.toggle('Scene');
                }} />
                <SidebarMenuItem title="NLE" icon={`nav-icon far fa-circle ${this.enabled('NLE')}`} onClick={() => {
                  this.toggle('NLE');
                }} />
                <SidebarMenuItem title="Chord Input" icon={`nav-icon far fa-circle ${this.enabled('Chord Input')}`} onClick={() => {
                  this.toggle('Chord Input');
                }} />
                <SidebarMenuItem title="Key Input" icon={`nav-icon far fa-circle ${this.enabled('Key Input')}`} onClick={() => {
                  this.toggle('Key Input');
                }} />
                <SidebarMenuItem title="Scales" icon={`nav-icon far fa-circle ${this.enabled('ScaleOptionList')}`} onClick={() => {
                  this.toggle('ScaleOptionList');
                }} />
                <SidebarMenuItem title="Chords" icon={`nav-icon far fa-circle ${this.enabled('ChordOptionLists')}`} onClick={() => {
                  this.toggle('ChordOptionLists');
                }} />
                <SidebarMenuItem title="On Deck Pallette" icon={`nav-icon far fa-circle ${this.enabled('OnDeckPallettes')}`} onClick={() => {
                  this.toggle('OnDeckPallettes');
                }} />
                <SidebarMenuItem title="Chord Pallette" icon={`nav-icon far fa-circle ${this.enabled('ChordPallette')}`} onClick={() => {
                  this.toggle('ChordPallette');
                }} />
                <SidebarMenuItem title="Quick Input" icon={`nav-icon far fa-circle ${this.enabled('QuickInputController')}`} onClick={() => {
                  this.toggle('QuickInputController');
                }} />
                <SidebarMenuItem title="Midi Files" icon={`nav-icon far fa-circle ${this.enabled('MidiFiles')}`} onClick={() => {
                  this.toggle('MidiFiles');
                }} />
                <SidebarMenuItem title="Midi Viewer" icon={`nav-icon far fa-circle ${this.enabled('MidiViewer')}`} onClick={() => {
                  this.toggle('MidiViewer');
                }} />
              </SidebarMenuItem>
            </SidebarMenu>
          </Sidebar>
        </SidebarContainer>
        <ContentWrapper>
          <ContentHeader></ContentHeader>
          <ContentMain>

            <SpacePane spaces={[{
              element: () => {
                if (this.isEnabled('ComposerHeader'))
                  return <ComposerHeader />
              }
            }, {
              element: () => {
                if (this.isEnabled('Scenes'))
                  return <Scenes />
              }
            }, {
              element: () => {
                if (this.isEnabled('Scene'))
                  return <Scene />
              }
            }, {
              element: () => {
                if (this.isEnabled('NLE'))
                  return <ChordProgressionNonLinearEditorView />
              },
            }, {
              element: () => {
                if (this.isEnabled('Chord Input'))
                  return <ChordInput />
              }
            }, {
              element: () => {
                if (this.isEnabled('Key Input'))
                  return <KeyInput />
              }
            }, {
              element: () => {
                if (this.isEnabled('ScaleOptionList'))
                  return <ScaleOptionList />
              }
            }, {
              element: () => {
                if (this.isEnabled('ChordOptionLists')) return <ChordOptionLists />
              }
            }, {
              element: () => {
                if (this.isEnabled('OnDeckPallettes')) return <OnDeckPallettes />
              }
            }, {
              element: () => {
                if (this.isEnabled('ChordPallette')) return <ChordPallette />
              }
            }, {
              element: () => {
                if (this.isEnabled('QuickInputController'))
                  return <QuickInputController />
              }
            }, {
              element: () => {
                if (this.isEnabled('MidiFiles'))
                  return <MidiFiles />
              }
            }, {
              element: () => {
                if (this.isEnabled('MidiViewer'))
                  return <MidiViewer />
              }
            }]} />
          </ContentMain>
          <ContentFooter />
          <ControlSideBar></ControlSideBar>
        </ContentWrapper>
      </div>
    );
  }
}

function NewButton() {
  let dispatch = useDispatch();
  return (
    <SidebarMenuItem onClick={() => {
      dispatch(newComposerCompanionStorage())
    }} title="New" />
  )
}
function SaveButton() {
  let dispatch = useDispatch();
  return (

    <SidebarMenuItem onClick={() => {
      dispatch(saveComposerCompanionStorage())
    }} title="Save" />
  )
}

function ExportSerato() {
  let dispatch = useDispatch();
  return (
    <SidebarMenuItem onClick={() => {
      dispatch(exportToSerato());
    }} title={"Export"} />
  )
}

class FileLoader extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <SidebarMenuItem title="Open"  >
        <LoadComposerCompanionFiles />
      </SidebarMenuItem>
    )
  }
}
function LoadComposerCompanionFiles() {
  let dispatch = useDispatch();
  return (

    <div id="drop_zone" style={{
      border: '5px solid blue',
      width: 200,
      whiteSpace: 'break-spaces',
      padding: 10,
      height: 100
    }}
      onDrop={(ev) => {
        console.log('File(s) dropped');

        // Prevent default behavior (Prevent file from being opened)
        ev.preventDefault();

        if (ev.dataTransfer.items) {
          // Use DataTransferItemList interface to access the file(s)
          let lastFile = null;
          let droppedFiles = [];
          for (var i = 0; i < ev.dataTransfer.items.length; i++) {
            // If dropped items aren't files, reject them
            if (ev.dataTransfer.items[i].kind === 'file') {
              var file = ev.dataTransfer.items[i].getAsFile();
              lastFile = file;
              console.log('... file[' + i + '].name = ' + file.name);
              droppedFiles.push(file);
            }
          }
          if (lastFile) {
            if (lastFile.name.endsWith('.cc')) {
              lastFile.text().then((txt) => {
                dispatch(loadComposerCompanionStorage({ data: txt }))
              }).catch(e => console.error(e))
            }
          }
          if (droppedFiles) {
            for (let i = 0; i < droppedFiles.length; i++) {
              readFile(droppedFiles[i]).then((data) => {
                midiService.loadMidi(droppedFiles[i].name, data);
                dispatch(loadMidiFile({ name: droppedFiles[i].name }))
              })
            }
          }
        } else {
          // Use DataTransfer interface to access the file(s)
          for (var i = 0; i < ev.dataTransfer.files.length; i++) {
            console.log('... file[' + i + '].name = ' + ev.dataTransfer.files[i].name);
          }
        }

      }} onDragOver={(ev) => {

        console.log('File(s) in drop zone');

        // Prevent default behavior (Prevent file from being opened)
        ev.preventDefault();
      }}>
      <p>Drag one or more files to this Drop Zone ...</p>
    </div>
  )
}