import React from 'react';

export default class SidebarMenuItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    active() {
        return this.props.active ? 'active' : '';
    }
    open() {
        return this.state.open ? 'menu-open' : '';
    }
    children() {
        return this.props.children && this.props.children.length ? true : false;
    }
    render() {
        return (<li className={`nav-item ${this.open()}`}>
            <a href="#" className={`nav-link ${this.active()}`} onClick={() => {
                if (this.props.children) {
                    this.setState({ open: !this.state.open });
                }
                if (this.props.onClick) {
                    this.props.onClick();
                }
            }}>
                <i className={`nav-icon ${this.props.icon || (this.children() && this.open() ? 'fas far fa-circle' : 'fas far fa-circle')}`}></i>
                <p>
                    {this.props.title}
                    {this.children() ? <i className="right fas fa-angle-left"></i> : null}
                </p>
            </a>
            {this.props.children && this.state.open ? (
                <ul className="nav nav-treeview">
                    {this.props.children}
                </ul>
            ) : null}
        </li>
        )
    }
}