let default_lang = 'en';
export function TitlesService(key, lang = null) {
    lang = lang || default_lang;


    if (lib[key] && lib[key][lang]) {
        return lib[key][lang];
    }
    return `[${key}]`;
}

const lib = {
    'octave-up': {
        en: 'Octave up'
    },
    'octave-down': {
        en: 'Octave down'
    }
};