import React from 'react';
import { Responsive, WidthProvider } from 'react-grid-layout';
import { flat, sharp } from '../../base/base-lib';
import StaffNotes from '../music/StaffNotes';
import { FormGroup } from '../FormGroup';
import { CHORD_PROGRESSION_SUB_DIVISIONS } from '../../features/composer/composerSlice';

const ResponsiveGridLayout = WidthProvider(Responsive);

export default class ChordProgressionNonLinearEditor extends React.Component {
    constructor(props) {
        super(props);

        let loadedLayout = {};

        this.state = {
            layout: {},
            currentLayoutKey: 'lg',
            loadedLayout
        }
    }
    componentDidMount() {

    }
    render() {
        let spaces = [];
        let measureDivisions = CHORD_PROGRESSION_SUB_DIVISIONS;
        let measures = this.props.chordProgression ? this.props.chordProgression.measures || 4 : 4;
        let total_divisions = measureDivisions * measures;
        for (let i = 0; i < measures; i++) {
            spaces.push((
                <div key={`measures${i}`} style={{ backgroundColor: '#2d2d2d' }} data-grid={{ x: i * measureDivisions, y: 0, w: measureDivisions, h: 1, static: true }}>
                    <span className="text"><h1>{i + 1}</h1></span>
                </div>
            ))
        }
        if (this.props.chordProgression && this.props.chordProgression.spaces) {
            spaces.push(...this.props.chordProgression.spaces.map((d, index) => {
                return (
                    <div key={`${d.id}`} data-grid={{ maxH: 1, ...d.position }}>
                        <ChordItem data={d}
                            selected={this.props.selectedItem === d.id}
                            onClick={() => {
                                if (this.props.onItemClick) {
                                    this.props.onItemClick(d);
                                }
                            }}
                            onDelete={() => {
                                if (this.props.onDelete) {
                                    if (this.state.loadedLayout) {

                                        this.setState({
                                            loadedLayout: {
                                                ... this.state.loadedLayout,
                                                [this.state.currentLayoutKey]: (this.state.loadedLayout[this.state.currentLayoutKey] || []).filter(v => v.i !== d.id)
                                            }
                                        }, () => {
                                            this.props.onDelete(d.id);
                                        });
                                    }
                                }
                            }}
                            onToggleStatic={() => {
                                if (this.props.onToggleStatic) {
                                    this.props.onToggleStatic(index)
                                }
                            }} bias={this.props.bias} />
                    </div>
                )
            }));
        }
        return (
            <div className="react-grid-layout-context" style={{ minHeight: 400 }}>
                <section className="content">
                    <ResponsiveGridLayout
                        className="layout"
                        draggableHandle=".drag-item"
                        verticalCompact={false}
                        preventCollision={true}
                        compactType={null}
                        measureBeforeMount={false}
                        rowHeight={50}
                        onLayoutChange={(currentLayout, allLayouts) => {
                            console.log('currentLayout');
                            console.log(currentLayout);
                            console.log('allLayouts');
                            console.log(allLayouts);
                            if (this.props.updateChordProgressionPositions) {
                                this.props.updateChordProgressionPositions(currentLayout);
                            }
                            // this.setState({
                            //     loadedLayout: {
                            //         ...allLayouts
                            //     }
                            // });
                        }}
                        onDrop={(layout, item) => {
                            console.log(layout);
                            console.log(item);
                        }}
                        breakpoints={{ lg: 1200 }}
                        cols={{ lg: total_divisions }}>
                        {spaces}
                    </ResponsiveGridLayout>
                </section>
            </div >
        )
    }
}

class ChordItem extends React.Component {
    render() {
        let chord_name = '';
        let chordInfoEl = null;
        let { props } = this;
        if (this.props.data) {
            let { chordData } = this.props.data;
            let { value } = chordData;
            chord_name = chordData.userTitle || chordData.title || chordData.name;
            if (chordData.chordInfo) {
                chord_name = chord_name || chordData.chordInfo.name;
            }
        }
        return (
            <div onClick={() => {
                if (this.props.onClick) {
                    this.props.onClick()
                }
            }}
                className={`card direct-chat direct-chat-warning box-header ${props.data.position.static ? '' : ' drag-item'}`}
                style={{
                    display: 'flex',
                    height: '100%',
                    paddingTop: 1,
                    paddingBottom: 1,
                    cursor: 'pointer'
                }}>
                <div className="card-body" title={chord_name} style={{
                    alignItems: 'center',
                    display: 'flex',
                    flex: 1,
                    alignContent: 'center',
                    justifyContent: 'center'
                }} >
                    <i className="fa fa-times" onClick={() => {
                        if (props.onDelete) {
                            props.onDelete();
                        }
                    }} style={{ position: 'absolute', right: 10, top: 0 }} />
                    {this.props.selected ? <i className="fa fa-star" style={{
                        position: 'absolute', left: 10, top: 0, color: '#f14411'
                    }} /> : null}
                    <h3 style={{
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap'
                    }}>{chord_name}</h3>
                </div>
            </div>
        )
    }
}